.banner {
  background-color: $black;
  
  h1, h3 {
    text-align: center;
  }
  
  h3 {
    padding-top: 6.2rem;
  }
  
  h1 {
    padding: 1.5rem 0;
  }
  
  .button-row {
    padding-bottom: 8.5rem;
  }
  
  .button-center {
    max-width: 30rem;
    margin: 0 auto;
    border: .4rem solid $magenta;
    border-radius: .8rem;
    cursor: pointer;
    
    p {
      text-align: center;
      font-family: $ff-gotham;
      font-size: 2.8rem;
      font-weight: 500;
      line-height: 0.75;
      color: $magenta;
      padding: 2rem 0 1.6rem 0;
    }
  }
  
  &_top {
    @include background-cover('../images/header-img', 'png', cover, 0 0, no-repeat);
  }
}

.restocking {
  display: flex;
  &-left, &-right {
    flex: 1;
  }
  &-left {
    padding-top: 2rem;
    .restocking-footer {
      margin-top: 3.5rem;
      p {
        @extend %center;
        text-align: center;
        font-style: italic;
        &:first-child {
          font-size: 2.6rem;
        }
        &:nth-child(2) {
          font-size: 3.6rem;
          font-weight: 700;
          color:$magenta;
        }
      }
    }
  }
  &-right {
    padding-top: 8rem;
  }
  .bottle {
    img {
      @extend %center;
      display: block;
      height: 100%;
      width: 82rem;
    }
  }
  h2 {
    margin-bottom: 2rem;
  }
  p {
    max-width: 76rem;
    margin-bottom: 5rem;
  }
  .wrap-receipe {
    margin-left: 2rem;
    max-width: 74rem;
    border-left:.4rem solid $grey;
    padding-left: 2rem;
  }
  .receipe {
    color: $grey;
    font-style: italic;
    line-height: 1.61;
    position: relative;
  }
}